	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"christopher is 8","date":"27 Dec 2007","slug":"/2007-12-27-christopher-is-8/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2140277711\" title=\"View 'CIMG0707' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2076/2140277711_ca6e6b341f.jpg\" alt=\"CIMG0707\" border=\"0\" width=\"375\" height=\"500\" /></a></p>\n<p>Christopher is 8, and the day begins with Pancake Cake &aacute; la <a href=\"http://www.amazon.co.uk/Pancakes-Findus-Pettson-Sven-Nordqvist/dp/190345879X/\">Findus</a>.</p>"},"next":{"fields":{"title":"8!","date":"26 Dec 2007","slug":"/2007-12-26-8/"}},"previous":{"fields":{"title":"Filmmaker Christopher","date":"30 Dec 2007","slug":"/2007-12-30-filmmaker-christopher/"}}},{"node":{"fields":{"title":"8!","date":"26 Dec 2007","slug":"/2007-12-26-8/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2139384766\" title=\"View 'CIMG0415' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2364/2139384766_f72128d33e.jpg\" alt=\"CIMG0415\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>We'll see <a href=\"http://www.papascott.de/archives/1999/12/27/christopher-ryan-hanson/\">tomorrow</a> how long it takes Christopher to notice the mirror.</p>"},"next":{"fields":{"title":"Mary and Joseph","date":"23 Dec 2007","slug":"/2007-12-23-mary-and-joseph/"}},"previous":{"fields":{"title":"christopher is 8","date":"27 Dec 2007","slug":"/2007-12-27-christopher-is-8/"}}},{"node":{"fields":{"title":"Mary and Joseph","date":"23 Dec 2007","slug":"/2007-12-23-mary-and-joseph/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2131497988\" title=\"View 'CIMG0412' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2043/2131497988_eff810f30c.jpg\" alt=\"CIMG0412\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>This is Mary and Joseph, along with an unidentified donkey. They've had a long hard journey from Nazareth to Bethlehem, but now they are just centimeters away from their goal. Christopher placed them on the kitchen table two weeks ago, and every day has moved them closer to the nativity scene near the back door. Two nights ago they were on the TV set. Today they made it on top of the radiator.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2131498374\" title=\"View 'CIMG0411' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2305/2131498374_49a929442f.jpg\" alt=\"CIMG0411\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>Here's wishing all of our readers that you find whatever you're looking for this season, even if it takes a two week journey from the kitchen table to the radiator. Merry Christmas!</p>"},"next":{"fields":{"title":"Christopher Teaches Chess","date":"22 Dec 2007","slug":"/2007-12-22-christopher-teaches-chess/"}},"previous":{"fields":{"title":"8!","date":"26 Dec 2007","slug":"/2007-12-26-8/"}}}],"pathPrefix":"","first":false,"last":false,"index":90,"pageCount":1101}}

